.hero--krane {
	padding: 264px 0 340px;
	background: linear-gradient(0deg, rgba(255, 255, 255, 0.04), rgba(255, 255, 255, 0.04)), #181E29;


	@media(min-width: 1025px) {
		.img-wrapper {
			transform:translate(20px ,-40%);
			position: absolute;
			left: 50%;	
			bottom: 0;

			.lottie {
				// height: 480px;
				// width: 470px;
				// position: relative;

				>div {
					right: 0;
				}
			}
		}
	}

	.mountain1 {
		height: 253px;
	}

	.mountain2 {
		height: 179px;
	}

	h1 {
		margin-bottom: 20px;
	} 
	p {
		margin-bottom: 50px;
	}

	.flag {
		display: flex;
		align-items: center;
		color: #679BFF;
		font-weight: 700;
		font-size: 16px;
		line-height: 22.4px;
		margin-bottom: 10px;

		img {
			width: 22px;
			margin-right: 5px;
		}
	}

	@media(max-width: 1439px) and (min-width: 1025px) {
		padding: 224px 0 245px;

		.img-wrapper {
			transform:translate(20px, -35%);
			width: 43%;
		}

		.mountain1 {
			height: 171px;
		}

		.mountain2 {
			height: 140px;
		}
	}

	@media(max-width: 1024px) {
		padding: 156px 0 128px;

		h1 {
			margin-bottom: 20px;
		} 
		p {
			margin-bottom: 20px;
			max-width: 500px;
		}

		.img-wrapper {
			margin: 0 auto;
			max-width: 300px;
		}

		.mountain1 {
			height: 147px;
		}

		.mountain2 {
			height: 106px;
		}

		.flag {
			justify-content: center;
		}
	}

	@media(max-width: 610px) {
		padding: 126px 0 84px;

		.img-wrapper {
			margin: 0 auto;
		}


		.mountain1 {
			height: 112px;
		}

		.mountain2 {
			height: 81px;
		}

		.btn-wrapper {
			padding: 15px 30px;
		}
	}
}

.zig-zag {
	background-color: #181D29;
	padding: 40px 0 80px;

	h2.h1 {
		margin-bottom: 140px;
		@extend .hideUp;
		transition: all 1s ease;
	}

	h3 {
		margin-bottom: 10px;
	}

	&.active {
		h2 {
			@extend .fadeInUp;
		}

		article {
			@extend .fadeIn;
		}
	}

	@media(min-width: 611px) {
		h2.h1 {
			br {
				display: none
			}
		}
	}

	@media(max-width: 1439px) and (min-width: 1025px) {
		padding: 30px 0 80px;

		.container {
			padding: 0;
		}

		h2.h1 {
			margin-bottom: 80px;
		}
	}

	@media(max-width: 1024px) {
		padding: 50px 0 80px;

		h2.h1 {
			margin-bottom: 100px;
		}

		h3 {
			font-size: 24px;
			line-height: 30px;
		}

		article {
			justify-content: space-between;
		}

		.zig-zag__content {
			width: 48%;
		}

		.img-wrapper {
			margin-bottom: 10px;
			justify-content: center;
			flex-direction:row;

			img {
				width: 220px;
				display: inline-block;
			}
		}
	}

	@media(max-width: 610px) {
		padding: 20px 0 80px;

		.zig-zag__content {
			width: 100%;
			text-align: center;
		}

		.img-wrapper {
			margin: 0 auto;
		}

		h2.h1 {
			margin-bottom: 50px;
			text-align: center;
		}
	}
}

.zig-zag__wrapper {

	h2 {
		margin-bottom: 10px;
	}

	p {
		margin-bottom: 0;
	}

	article {
		display: flex;
		@extend .hide;


		+ article {
			margin-top: 40px;
		}

		>div {
			width: 50%;
			display: flex;
			flex-direction: column;
			justify-content: center;
		}

		&:nth-child(1) {
			transition: all 1s ease .6s;
		}
		&:nth-child(2) {
			transition: all 1s ease 1.2s;
		}
		&:nth-child(3) {
			transition: all 1s ease 1.8s;
		}
	}

	.img-wrapper {
		display: flex;
		align-items: flex-end;
	}

	@media(max-width: 610px) {
		article {
			flex-direction: column;

			&:not(:nth-child(even)) {
				flex-direction: column-reverse;
			}

			+ article {
				margin-top: 50px;
			}

			>div {
				width: 100%;
			}
		}
	}
}

.accordion {
	background: #EAEAEA;
	padding: 140px 0;

	.heading {
		margin-bottom: 100px;
		@extend .hideUp;
		transition: all 1s ease .2s;


		h2 {
			// font-size: 48px;
			// line-height: 52.8px;
			color: #111;


			@media(min-width: 610px) {

				br {
					display: none;
				}
			}
		}
		@media(max-width: 1024px) {
			margin-bottom: 60px;

			h2 {
				text-align: left;
				font-size: 48px;
				line-height: 52.8px;
				margin-bottom: 0;
			}
		}
	} 

	.tab-content {
		display: block;
		min-height: auto;
		overflow: hidden;
		opacity: 1;
		visibility: visible;
		// @extend .hideUp;

		&:nth-child(1) {
			transition: all 1s ease .2s;
		}
 
		&:nth-child(2) {
			transition: all 1s ease .4s;
		}

		&:nth-child(3) {
			transition: all 1s ease .6s;
		}

		&:nth-child(4) {
			transition: all 1s ease .8s;
		}

		&:not(:last-child) {
			margin-bottom: 70px;

			@media(max-width: 1439px) and (min-width: 1025px) {
				margin-bottom: 50px;
			}

			@media(max-width: 1024px) {
				margin-bottom: 40px;
			}

			@media(max-width: 610px) {
				margin-bottom: 20px;
			}
		}
	}

	.tab-content__text {
		padding: 60px;

		@media(max-width: 1439px) and (min-width: 1025px) {
			padding: 40px;

			.read-more {
				font-size: 18px;
				line-height: 25.2px;
			}
		}

		@media(max-width: 1024px) {
			padding: 40px;

			h3 {
				text-align: left;
				max-width: initial;
				margin: 0 0 20px;
			}
		}

		@media(max-width: 610px) {
			padding: 20px;

			h3 {
				text-align: left;
			}
		}
	}

	.tab-content__img {
		padding: 0;
		background: #181D29;
		overflow: hidden;

		.img-wrapper {
			background-size: 100% auto;
			background-position: right 0;

			img.sticker {
				width: 100%;
				// position: -webkit-sticky;
				// position: sticky;
				// top: 50%;
				z-index: 2;
			}

			.img-mob {
				display: none;
			}

			@media(max-width: 1024px) {
				padding-bottom: 0;

				.uk-sticky-placeholder {
					display: none;
				}

				.img-mob {
					display: block;
				}
			}
			@media(max-width: 610px) {
				padding-bottom: 0;
			}
		}

		@media(max-width: 1024px) {
			margin-bottom: 20px;
		}

		@media(max-width: 610px) {
			margin-bottom: 20px;
		}
	}

	h3.h2 {
		margin-bottom: 20px;
		color: #333;
	}

	h3:not(.h2) {
		margin-bottom: 20px;
		color: #333333;
	}
	p {
		margin-bottom: 28px;

		@media(max-width: 1024px) {
			margin-bottom: 15px;
		}

		@media(max-width: 610px) {
			margin-bottom: 15px;
		}
	}

	.read-more {
		font-size: 24px;
		line-height: 33.6px;
		display: inline-block;
		font-weight: 700;

		&:hover {
			text-decoration: underline;
		}

		@media(max-width: 1024px) {
			font-size: 18px;
			line-height: 25.2px;
		}

		@media(max-width: 610px) {
			
			font-size: 18px;
			line-height: 25.2px;
		}
	}

	@media(max-width: 1439px) and (min-width: 1025px) {
		padding: 120px 0;
		
		.heading {
			margin-bottom: 80px;
		}
	}

	@media(max-width: 1024px) {
		padding: 100px 0;

		h2 {
			font-size: 32px;
			line-height: 35.2px;
			text-align: center;
			margin-bottom: 40px;
		}

		.tab-content__img {
			height: 270px;

			.uk-sticky-placeholder {
				display: block!important;

				img {
					position: absolute!important;
					top: 50%!important;
					transform:translate(0,-50%)!important;
				}
			}
		}

		.accordion__content-item {
			h3 {
				max-width:	100%;
				text-align: left;
			}
		}
	}

	@media(max-width: 610px) {
		padding: 80px 0;

		.tab-content__img {
			height: 144px;
		}


		.heading {
			margin-bottom: 40px;

			h2 {
				font-size: 32px;
				line-height: 35.2px;
				text-align: left;
				margin-bottom: 40px;
			}
		} 
	}

	&.active {
		.heading{
			@extend .fadeInUp;
		}
	}
}

.accordion__content {
	display: none;
	margin-top: 60px;

	ul,p {
		margin-bottom: 0;
	}

	li:not(:last-child) {
		margin-bottom: 10px;
	}

	.tab-content__list {

		li {
			padding-left: 36px;

			&:before {
				top: 8px;
				width: 20px;
				height: 20px;
				border-radius: 50%;
				background-color: #C4C4C4;
			}

			@media(max-width: 1024px) {
				padding-left: 31px;

				&:before {
					top: 6px;
					width: 16px;
					height: 16px;
				}

			}
		}
	}

	.list-checked {
		li {
			&:before {
				background: #6FCF97;
				background-image: url('../images/check.svg');
				background-repeat: no-repeat;
				background-position: 50% 50%;

				@media(max-width: 1024px) {
					background-size: 63% 100%;
				}
			}
		}
	}
 
	.list-danger {
		li {
			&:before {
				background: #EB5757;
			}
		}
	}

	@media(max-width: 1024px) {
		margin-top: 30px;
	}

	@media(max-width: 610px) {
		margin-top: 20px;
	}
}

.accordion__content-item:not(:last-child) {
	margin-bottom: 60px;

	@media(max-width: 1024px) {
		margin-bottom: 30px;
	}

	@media(max-width: 610px) {
		margin-bottom: 20px;
	}
}

.subscribe--krane {
	padding-top: 140px;

	.subscribe-box__content {
		width: 63.7%;
		background-image: url('../images/bounds1.png');
		padding-bottom: 60px;
	}

	button {
		background-color: #2B55C2;
	}

	.subscribe-box__img {
		background-position: 0 calc(100% + 23px);
		background-size: 100%;
	}

	@media(max-width: 1439px) and (min-width: 1025px) {
		padding-top: 120px;

		.subscribe-box__content {
			padding: 50px;
			width: 80%;

			.large-text {
				max-width: 290px;
			}
		}

		.subscribe-box__img {
			background-position: 0 100%;
		}

		.subscribe-box {
			form {
				margin-top: 20px;
				margin-bottom: 15px;
			}
		}

		button {
			span {
				display: inline-block;
			}
		}
	}

	@media(max-width: 1024px) {
		padding-top: 100px;

		button {
			span {
				display: none;
			}
		}

		.subscribe-box__img {
			background-position: 0 100%;
		}

		.subscribe-box__content {
			padding: 30px;
			padding-right: 90px;

			form {
				max-width: 364px;
				margin-bottom: 15px;
				margin-top: 20px;
			}

			p {
				max-width: 95%;
			}
		}
	}

	@media(max-width: 610px) {
		padding-top: 80px;

		.subscribe-box {
		    flex-direction: column-reverse;
		}

		.subscribe-box__content {
			background-image: url('../images/dark-bgr1.png');
			width: 100%;
			padding-top: 40px;
			padding-bottom: 30px;
			padding-right: 30px;

			p {
				max-width: 95%;
			}

		}

		.subscribe-box__img {
			background-position: 100% 100%;
			background-size: auto;

		}
	}
}

