

.hero--light {
	background-color: #EAEAEA;
	padding: 264px 0 0;
	margin-bottom: -1px;

	.container {
		flex-direction: column;
	}

	article {
		width: 100%;
		text-align: center;
	}

	h1 {
		color: #121212;
		margin-bottom: 30px;
	}

	p {
		color: rgba(18, 18, 18, 0.7);
		margin-bottom: 45px;
	}

	.img-wrapper {
		position: relative;
		left: 0;
		right: auto;
		bottom: auto;
		transform: translate(0, 0);
		margin-top: 15px;
		width: 1240px;
	}

	.lottie {
		display: block;
	}

	.lottie-mobile {
		display: none;
	}

	@media(max-width: 1439px) and (min-width: 1025px) {

		padding: 224px 0 40px;

		h1 {
			margin-bottom: 20px;
		}

		p {
			margin: 0 auto 30px;
			max-width: 540px;
		}

		.img-wrapper {
			width: 90vw;
			margin-top: 80px;
		}
	}

	@media(max-width: 1024px) {
		padding: 186px 0 40px;

		h1 {
			margin-bottom: 20px;
		}

		p {
			margin-bottom: 30px;
			max-width: initial;
		}

		.img-wrapper {
			margin-top: 60px;
			max-width: calc(100% + 60px);
			margin-bottom: 0;
			transform:translate(-30px, 0);
		}

	}

	@media(max-width: 610px) {
		padding: 111px 0 0;

		.lottie {
			display: none;
		}

		.lottie-mobile {
			display: block;
		}

		.img-wrapper {
			width: 95vw;
			margin: 30px auto;
			display: block;
			transform:translate(0, 0);

		}

		a.button {
			height: 36px;
			font-size: 14px;
			line-height: 19.6px;
			padding: 0 12px;
		}

		h1 {
			margin-bottom: 20px;
		}

		p {
			margin-bottom: 30px;
			max-width: 84%!important;
		}
	}
}

.zig-zag--light {
	background: #EAEAEA;
	padding: 140px 0;

	.heading {
		max-width: 670px;
		margin: 0 auto 140px;
		text-align: center;

		h2 {
			margin-bottom: 30px;
		}

		p {
			margin-bottom: 0;
		}
	}

	p {
		color: #333;
	}

	.zig-zag__wrapper {

		article {
			justify-content: space-between;
		}

		article:nth-child(odd) {

			.zig-zag__content {
				margin-right: 30px;
			}
		}

		article:nth-child(even) {
			
			.zig-zag__content {
				margin-left: 30px;
			}
		}

		article + article {
			margin-top: 140px;
		}

		.img-wrapper {
			img {
				max-width: 500px;
				height: auto;
			}
		}

		.zig-zag__content {
			width: 46%;
			padding: 0 20px;
		}
	}

	@media(max-width: 1439px) and (min-width: 1025px) {
		padding: 120px 0;

		.heading {
			max-width: 520px;
			margin: 0 auto 120px;
		}

		.zig-zag__wrapper {

			.img-wrapper {
				width: 394px;
				max-width: 100%;

				img {
					height: auto;
					width: 100%
				}
			}

			article + article {
				margin-top: 120px;
			}
		}
	}

	@media(max-width: 1024px) {
		padding: 100px 0 ;

		.heading {
			max-width: 500px;
			margin: 0 auto 100px;
		}

		.zig-zag__wrapper {

			.img-wrapper {
				width: 330px;
				max-width: 100%;

				img {
					height: auto;
					width: 100%
				}
			}

			article + article {
				margin-top: 100px;
			}
			.zig-zag__content {
				width: 49%;
			}
		}

	}

	@media(max-width: 610px) {
		padding: 80px 0 ;

		.heading {
			margin: 0 auto 80px;
		}

		.zig-zag__wrapper {

			.img-wrapper {
				margin-bottom: 20px;
				width: 100%;
			}

			article:nth-child(odd) {

				.zig-zag__content {
					margin-right: 0;
				}
			}

			article:nth-child(even) {
				
				.zig-zag__content {
					margin-left: 0;
				}
			}

			article + article {
				margin-top: 50px;
			}
			.zig-zag__content {
				// text-align: center;
				width: 100%;
				padding: 0;
				text-align: left;
			}
		}
	}
}

