.team-slider {
	background: linear-gradient(0deg, rgba(18, 18, 18, 0.06), rgba(18, 18, 18, 0.06)), #F4F4F4;
	padding: 140px 0 40px;

	h2 {
		margin-bottom: 80px;
	}

	h3 {
		// padding: 30px 0 0;
		margin-bottom: 0;
		color: #333333;
	}

	h4 {
		// padding: 0 30px;
		margin-bottom: 10px;
		color: #333333;
	}

	.slider {
		margin-bottom: 30px;

		.item {
			height: 100%;
		}
	}

	.card {
		background-color: #fff;
	}

	.social-list {
		display: flex;
		align-items: center;
		margin-left: 0;
		margin-bottom: 0;

		li {
			margin-right: 10px;
		}

		a {
			color: #7315CD;
			height: 44px;
			width: 44px;
			display: flex;
			align-items: center;
			justify-content: center;
			font-size: 25px;

			&:hover {
				color: darken(#7315CD, 10%);
			}
		}
	}

	.owl-nav {
		button {
			position: absolute;
			top: 50%;
			opacity: .6;
			transition: all .3s ease;
			outline:none;

			&:hover {
				opacity: 1;
			}

			&.disabled {
				filter: grayscale(100%);
				opacity: .2;
			}
		}

		.owl-prev {
			left: 0;
			transform:translate(-39px,-50%) rotate(180deg);
		}
		.owl-next {
			right: 0;
			transform:translate(39px,-50%);
		}

		@media(max-width: 1024px) {
			.owl-prev {
				transform:translate(-20px,-50%) rotate(180deg);
			}
			.owl-next {
				transform:translate(20px,-50%);
			}
		}
	}

	.card__content {
		display: flex;
		flex-direction: column;
		padding: 30px;
	}

	.card__image-wrapper {

		.social-list {
			display: none;
		}
	}

	.card__image {
		margin-bottom: 20px;
	}

	.card__text {

		p {
			color: rgba(51, 51, 51, 0.7);
			margin-bottom: 20px;
		}

		.social-list {
			display: flex;
		}
	}

	@media(max-width: 1024px) {
		padding: 100px 0 40px;

		h2 {
			margin-bottom: 80px;
			font-size: 48px;
			line-height: 52.8px;
		}

		.card__content {
			padding: 15px;
		}

		.card--image {
			min-height: 450px;
			position: relative;
		}

		.card__text {

			.social-list {
				position: absolute;
				left: 15px;
				bottom: 15px;
			}
		}

	}

	@media(max-width: 610px) {
		padding: 80px 0 40px;

		br {
			display: none;
		}

		.slider {
			margin-bottom: 25px;
		}

		.container {
			padding: 0; 
		}

		.card {
			// background-color: transparent;
			margin-bottom: 0;	
		}

		.item {
			&+ .item {
				margin-top: 30px;	
			}
		}

		.heading {
			font-size: 32px;
			line-height: 35.2px;
			margin-bottom: 30px;
		}

		.card__text,h3, h4 {
			padding: 0;	
		}

		.card__content {
			padding: 15px;
		}

		.card__image-wrapper {
			overflow: hidden;

			.social-list {
				display: flex;
			}

			svg {
				width: 24px;
			}

			h3 {
				margin-bottom: 5px;
				margin-top: 12px;
			}

			h4 {
				font-size: 14px;
				line-height: 22.4px;
				margin-bottom: 0;
			}
		}

		.card__image {
			width: 100px;
			height: 100px;
			padding: 0;
			float: left;
			margin-right: 10px;
			margin-bottom: 15px;

		}

		.card--image {
			min-height: 0;
		}

		.card__text {

			p {
				margin-bottom: 0;
				font-size: 14px;
				line-height: 22.4px;
			}
			.social-list {
				display: none;
			}


		}
	}
}