
.popup-aside {
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	z-index: 99999;
	pointer-events: none;

	.overlay {
		height: 100%;
		background-color: rgba(#000, .3);
		transition: all .3s ease;
		visibility: hidden;
		opacity: 0;
	}


	&.is-active {

		pointer-events: auto;

		.overlay {
			visibility: visible;
			opacity: 1;
		}

		.popup__content {
			transform: translate(0, 0);
		}
	}
}

.popup__content {
	background: #F4F4F4;
	position: absolute;
	right: 0;
	top: 0;
	padding: 80px 0 48px 80px;
	width: 600px;
	height: 100%;
	transform: translate(100%, 0);
	transition: all .3s ease;

	article {
		overflow-y: auto;
		overflow-x: hidden;
		height: 100%;
		padding-right: 80px;
	}

	h2 {
		margin-bottom: 4px;
		text-align: center;
	}

	h3 {
		margin-bottom: 30px;
		text-align: center;
		color: #333;
	}	

	.row {
		margin-left: -15px;
		margin-right: -15px;

		>div {
			padding: 0 15px;
			margin-bottom: 15px;
		}

		.button {
			width: 100%;

			svg {
				margin-right: 10px;
			}
		}
	}

	.line-center {
		text-align: center;
		margin: 15px 0 30px;
		position: relative;

		&:before {
			left: 0;
			top: 50%;
			transform: translate(0,-50%);
			content: "";
			position:absolute;
			width: 100%;
			height: 2px;
			background-color: rgba(#121212, .1);
		}

		span {
			background: #F4F4F4;
			padding: 0 10px;
			color: rgba(#333, .7);
			position: relative;
			z-index: 5;
		}
	}

	.input-box {
		margin-bottom: 30px;

		input {
			&:hover,
			&:focus {
				border-color: #7315CD;
			}
		}
	}

	.check-box {
		margin-bottom: 30px;
	}

	p {
		color: #333;

		a {
			&:hover {
				text-decoration: underline;
			}
		}
	}

	.divider {
		margin-top: 40px;
		margin-bottom: 30px;
		width: 100%;
		height: 2px;
		background: #EAEAEA;
	}
}

.password-strength {
	display: flex;
	align-items: center;
	margin-top: -20px;
	margin-bottom: 30px;

	p {
		margin-right: 20px;
		margin-bottom: 0;
	}

	ul {
		flex:1;
		display: flex;
		align-items: center;
		margin-bottom: 0;
	}

	li {
		height: 2px;
		width: 25%;
		background-color: rgba(51, 51, 51, 0.35);

		&.fill {
			background-color: #7315CD;
		}
			
		& + li {
			margin-left: 8px;
		}
	}
}

.request-access {
	.popup__content {
		width: 560px;
	}
}