.footer-main {
	background: linear-gradient(0deg, rgba(255, 255, 255, 0.08), rgba(255, 255, 255, 0.08)), #121212;
	padding-top: 82px;
	padding-bottom: 30px;
	overflow: hidden;

	&.active {
		.text,
		.products-list li,
		.link-list li,
		.social-list li,
		.big-link,
		.slack-box
		 {
			@extend .fadeInUp;
		}
	}

	@media(max-width: 1024px) {
		padding-top: 61px;
		padding-bottom: 20px;

		.container {
			padding: 0;
		}
	}

	  @media(max-width: 768px) and (min-width: 611px) {

	  	.container {
		    padding: 0 20px;
		    padding-right: 30px;
	  	}
	  }

	@media(max-width: 610px) {

		padding-top: 41px;
		padding-bottom: 30px;
	}

	.social-list {
		display: flex;
		align-items: center;
		margin-bottom: 30px;

		li + li {
			margin-left: 30px;
		}

		a {
			width: 36px;
			font-size: 29px;
			display: flex;
			align-items: center;

			&:hover {
				color: #A467DE;
			}
		}

		li {

			&:nth-child(1) {
				transition: all 1s ease 1s;
				@extend .hideUp;
			}
			&:nth-child(2) {
				transition: all 1s ease 1.1s;
				@extend .hideUp;
			}
			&:nth-child(3) {
				transition: all 1s ease 1.2s;
				@extend .hideUp;
			}
			&:nth-child(4) {
				transition: all 1s ease 1.3s;
				@extend .hideUp;
			}

		}

		@media(max-width: 1024px) {
			margin-bottom: 26px;

			li + li {
				margin-left: 27px;
			}

			a {
				width: 24px;
				font-size: 20px;
			}
		}

		@media(max-width: 610px) {
			margin-bottom: 20px;

			li + li {
				margin-left: 42px;
			}

			a {
				font-size: 18px;
			}
		}
	}

	.big-link {
		font-size: 24px;
		line-height: 33.6px;
		font-weight: 700;
		display: inline-block;
		margin-bottom: 40px;

		transition: all 1s ease 1.4s;
		@extend .hideUp;

		&:hover {
			color: #fff!important;
		}

		@media(max-width: 1439px) and (min-width: 1025px) {
			font-size: 18px;
			line-height: 25.2px;
		}

		@media(max-width: 1024px) {
			font-size: 18px;
			line-height: 25.2px;
			margin-bottom: 20px;
		}

		@media(max-width: 610px) {
			margin-bottom: 40px;
		}
	}

}

.footer-main__content {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;

	@media(max-width: 1024px) {
		.footer-main__item {

			&:first-child {
				width: 100%;

				p {
					margin-bottom: 40px;
				}
			}
		}

		.footer-main__logo {
			margin-bottom: 11px;
		}
	}

	@media(max-width: 610px) {

		.footer-main__item {
			width: 100%;

			// &:nth-child(2) {
			// 	h3 {
			// 		display: none;
			// 	}
			// }
		}

		.footer-main__logo {
			margin-bottom: 9px;

			svg {
				width: 56px;
				height: 22px;
			}
		}

		.footer-main__item-pages {
			ul {
				display: flex;
				flex-wrap: wrap;

				li {
					width: 50%;
				}
			}
		}
	}
}

.footer-main__logo {
	margin-bottom: 32px;
	display: inline-block;
}

.footer-main__item {
	h3 {
		color: rgba(255, 255, 255, 0.35);
		margin-bottom: 30px;
	}

	.text {
		transition: all 1s ease .2s;
		@extend .hideUp;
	}
 
	.mb26 {
		margin-bottom: 26px;
	}

	@media(max-width: 1024px) {

		h3 {
			margin-bottom: 20px;
		}

		.mb26 {
			margin-bottom: 20px;
		}
	}
}

.products-list,
.link-list {
	li {
		margin-bottom: 30px;

		@media(max-width: 610px) {
			margin-bottom: 20px!important;
		}
	}
}

.products-list {
	li {

		&:nth-child(1) {
			transition: all 1s ease .2s;
			@extend .hideUp;
		}
		&:nth-child(2) {
			transition: all 1s ease .3s;
			@extend .hideUp;
		}
		&:nth-child(3) {
			transition: all 1s ease .4s;
			@extend .hideUp;
		}
		
	}

	@media(max-width: 610px) {
		display: flex;
		justify-content: space-between;

		li:not(:last-child) {
			margin-right: 20px;
		}

		.img-wrapper {
			margin-right: 0;
			margin-bottom: 10px;
			width: 28px;
		}

		.flag-box {
			flex-direction: column;
			align-items: flex-start;
		}
	}
}

.link-list {
	li {

		&:nth-child(1) {
			transition: all 1s ease .5s;
			@extend .hideUp;
		}
		&:nth-child(2) {
			transition: all 1s ease .6s;
			@extend .hideUp;
		}
		&:nth-child(3) {
			transition: all 1s ease .7s;
			@extend .hideUp;
		}
		&:nth-child(4) {
			transition: all 1s ease .8s;
			@extend .hideUp;
		}
		&:nth-child(5) {
			transition: all 1s ease .9s;
			@extend .hideUp;
		}
		
	}
	a {
		font-weight: 700;

	}
	@media(max-width: 1024px) {
			
		a {
			line-height: 22.4px;
			font-size: 14px;
		}

		li {
			margin-bottom: 30px;
		}

	}

	@media(max-width: 610px) {
		li {
			margin-bottom: 10px;
		}
	}
}

.flag-box {
	display: flex;
	align-items: center;

	.img-wrapper {
		margin-right: 15px;
		width: 36px;

		@media(max-width: 610px) {
			width: 28px;
			margin-bottom: 8px;
		}
	}

}

.flag-box__content {

	h4 {
		margin-bottom: 2px;
		font-weight: 700;
		color: #fff;

		@media(max-width: 1024px) {

			font-size: 14px;
			line-height: 19.6px;
		}

	}

	p {
		margin-bottom: 0;
		font-weight: 600;
		font-size: 10px;
		line-height: 12px;
		text-transform: uppercase;

		@media(max-width: 1024px) {
			font-weight: 700;
		}
	}

	&.disabled {
		h4 {
			opacity: .35;
		}

		p {
			opacity: .7;
		}
	}
}

.slack-box {
	background: linear-gradient(0deg, rgba(255, 255, 255, 0.12), rgba(255, 255, 255, 0.12)), #121212;
	padding: 20px; 
	padding-top: 25px;
	padding-right: 25px;
	display: flex;
	align-items: flex-start;
	width: 310px;
	margin-left: -20px;
	margin-right: -20px;
	transition: all 1s ease 1.5s;
	@extend .hideUp;

	h4 {
		margin-bottom: 12px;
		line-height: 22.4px;
	}

	p {
		margin-bottom: 15px;
	}

	img {
		min-width: 32px;
		height: 32px;
		margin-left: 28px;
		margin-top: -5px;
	}

	a {
		font-weight: 700;
		line-height: 22.4px;

		&:hover {
			color: #fff!important;
		}
	}

	@media(min-width: 992px) {

		h4 {
			br {
				display: none!important;
			}
		}
	}

	@media(max-width: 1439px) and (min-width: 1025px) {

		a {
			font-size: 14px;
			line-height: 19.6px;
		}
	}

	@media(max-width: 1024px) {
		padding: 22px 15px 20px;

		img {
			position: absolute;
			right: 15px;
			top: 23px;
			margin-left: 0;
			min-width: 24px;
			height: 24px;
		}

		p {
			line-height: 22.4px;
			margin-bottom: 20px;
		}

		h4 {
			font-size: 14px;
			line-height: 19.6px;
			font-weight: 700;
			br {
				display: none;
			}
		}

		a {
			font-size: 14px;
			line-height: 19.6px;
		}
	}

	@media(max-width: 1024px) and (min-width: 768px) {
		max-width: 240px!important;
		margin-left: -15px!important;
		margin-right: -16px!important;
		position: relative;
	}

	@media(max-width: 610px) {
		min-width: 100%!important;
		width: calc(100% + 30px)!important;
		max-width: initial!important;
		margin-left: -15px!important;
		margin-right: -15px!important;

		padding: 22px 20px 20px 20px;

		h4 {
			margin-bottom: 7px;
			br {
				display: none;
			}
		}

		p {
			max-width: 230px;
			margin-bottom: 15px;
		}

		img {
			right: 20px;
			top: 20px;
			margin-top: 0;
		}
	}
}

.footer-main__subfooter {
	padding-top: 40px;
	display: flex;
	align-items: center;
	justify-content: space-between;

	ul {
		margin-bottom: 0;
		display: flex;
		flex-wrap: wrap;
		align-items: center;

		li {
			& + li {
				margin-left: 30px;
			}
		}
	}

	small,
	a,
	p {
		margin-bottom: 0;
		font-size: 14px;
		line-height: 19.6px;
	}

	a.opacity70 {
		&:hover { 
			opacity: 1!important;
		}
	}

	@media(max-width: 1024px) {
		padding-top: 30px;

		small,
		a,
		p {
			font-size: 12px;
			line-height: 16.8px;
			white-space: nowrap;
		}
	}

	@media(max-width: 610px) {
		align-items: flex-end;
		padding-top: 30px;

		p {
			margin-left: -15%;
		}

		ul {
			li {
				& + li {
					margin-left: 0;
				}

				&:first-child {
					width: 100%;
					margin-bottom: 10px;
				}

				&:last-child {
					margin-left: 20px;
				}
			}
		}
	}
}