

.animation-container {
	padding: 62px 75px;
	background: linear-gradient(0deg, rgba(255, 255, 255, 0.16), rgba(255, 255, 255, 0.16)), #121212;

	p {
		transition:all .3s ease;
	}

	&.is-active1 {
		.cloud-element {

			&.cloud-element1,
			&.cloud-element2,
			&.cloud-element3 {

				.purple-line1 {
					stroke: lighten(#424242,15%);
				}

				.under-item {
					path {
						fill: lighten(#424242,15%);
					}
				}
			}

			&.cloud-element3 {

				.under-item {
					path {
						fill: #A467DE;
						animation: changeColorCloudElement3 3.8s ease-in;
					}
				}
				.under-item-wrapper {

					&:before {
						animation: waveOne 1s ease-in 3.3s;
					}

					&:after {
						animation: waveTwo 1s ease-in 3.3s;
					}
				}
			}

			&.cloud-element4 {

				.bottom-item-border {
					animation: cloudElement4Border 3s ease-in .6s;
				}

				.under-item-wrapper {
					animation: under-item-animate 2.4s ease-in;

					&:before {
						animation: waveOne 1s ease-in 3.3s;
					}

					&:after {
						animation: waveTwo 1s ease-in 3.3s;
					}
				}

				p {
					animation: moveTop 2.4s ease-in;
				}
			}
		}

		.base-elements-right {
			.button-box {
				.line {
					span {
						&:before {
							animation: buttonBoxVisibleLine 2.7s ease-in .5s;
						}
					}
					&:before {
						animation: buttonBoxComet 3s ease-in .2s;
					}
				}
			}
		}
	}
}

.base-elements {
	display: flex;
	justify-content: space-between;
	align-items: flex-end;

	.button-box {
		width: 100%;
		height: 74px;
		display: flex;
		align-items: center;
		justify-content: center;
		font-weight: 600;
		font-size: 24px;
		color: rgba(255, 255, 255, 0.7);
		background: #424242;
		margin-bottom: 100px;
		position: relative;

		.line {
			position: absolute;
			left: 50%;
			margin-left: -4px;
			top: 100%;
			width: 8px;
			height: 100px;
			overflow: hidden;

			span {
				background-color: #555555;
				width: 2px;
				height: 100%;
				position: relative;
				display: block;
				left: 50%;
				transform: translate(-50%, 0);
				top: 0;

				&:before {
					position: absolute;
					content:"";
					width: 100%;
					height: 100%;
					left: 0;
					top: 0;
					background-color: #A467DE;
					// transition: all .3s ease .8s;
					visibility: hidden;
					opacity: 0
				}
			}

			&:before {
				position: absolute;
				content: '';
				width: 8px;
				height: 100%;
				left: 50%;
				top: 0;
				transform: translate(-50%, -100%);
			 	// background: rgb(164,103,222);
				background: linear-gradient(180deg, rgba(164,103,222,0) 0%, rgba(164,103,222,1) 100%);
				border-radius: 4px;
				z-index: 5;
				// transition: all .3s ease .5s;
			}

		}
	}

	.base-element {
		width: 244px;
		display: flex;
		flex-direction: column;
		align-items: center;

		.icon-wrapper {
			margin-bottom: 38px;
		}
	}

	&-left { 

		.icon-wrapper {
			width: 107px;
		}
	}

	&-center {
		text-align: center;

		p {
			width: 300px;
			color: rgba(255, 255, 255, 0.7);
			font-size: 20px;
			line-height: 28px;
			font-weight: 600;
			margin-bottom: 35px;
		} 
	}

	&-right {

		.icon-wrapper {
			width: 140px;
		}
	}
}

.four-elements {
	display: flex;
	justify-content: space-between;
}

