.cloud-element {
	position: relative;
	// margin-right: -3px;

	.bottom-item-border {
		transition: all 2s ease-in 0;
		stroke-dasharray: 1000;
		stroke-dashoffset: 1000;
		// animation: dash 5s linear infinite;
		stroke-width: 4;
	}

	.top-item {
		position: absolute;
		z-index: 3;
		left: 50%;
		top: 50%;
		transform:translate(-50%,-50%);
	}

	.bottom-item {
		position: relative;
		z-index: 2;
		transition:all .3s ease;

	}

	.under-item {

	}

	

	.under-item-wrapper {
		position: absolute; 
		left: 50%;
		top: 0;
		transform:translate(-50%, 10px);
		// transition:all .3s ease;

		&:before {
			position: absolute;
			border:2px solid #A467DE;
			width: 80%;
			height: 80%;
			content:'';
			left: 50%;
			top: 50%; 
			transform: translate(-50%,-50%) skew(60deg, 150deg);
			visibility: hidden;
			opacity: 0;
		}

		&:after {
			position: absolute;
			border:2px solid #A467DE;
			width: 60%;
			height: 60%;
			content:'';
			left: 50%;
			top: 50%; 
			transform: translate(-50%,-50%) skew(60deg, 150deg);
			visibility: hidden;
			opacity: 0;
		}
	}

	.icon-wrapper {
		position: relative;
	}

	

	p {
		font-weight: 600;
		font-size: 20px;
		color: rgba(255, 255, 255, 0.7);
		text-align: center;
		margin-bottom: 0;
		margin-top: 20px;
	}
	
	.line {
		position: absolute;
		background-color: #555555;
		width: 2px;
		height: 100px;
	}

	&1 {
		margin-left: -8px;

		.top-item {
			transform:translate(-47%,-53%);
		}

		.line {
			left: 97%;
			top: 52%;
			transform:rotate(120deg);
			height: 142px;
		}

	}

	&2 {
		margin-top: 145px;
		margin-left: -100px;

		.top-item {
			transform:translate(-50%,-55%);
		}

		
	}
	&3 {
		margin-top: 145px;
		margin-right: -100px;

		.line {
			right: 117%;
			top: 17%;
			transform:rotate(90deg);
			height: 100px;
		}

		.top-item {
			transform:translate(-50%,-68%);
		}
	}

	&4 {
		margin-right: -8px;

		.line {
			right: 97%;
			top: 52%;
			transform:rotate(-120deg);
			height: 142px;
		}
	}
}