.dropdown-box {
	width: 100%;
	height: 100%;
	position: fixed;
	left: 0;
	top: 0;
	z-index: 10;
	display: none;
	
	&.is-active {
		display: block;
	}
}

.bar-box {
	width: 100%;
	height: 100%;
	position: fixed;
	left: 0;
	top: 0;
	z-index: 10;
	visibility: hidden;
	opacity: 0;
	background: #383838;
	transition:all .3s ease;

	&.bar-box--krane {
		background-color: #2f343e;
	}

	&.bar-box--light {
		background: linear-gradient(0deg, rgba(18, 18, 18, 0.08), rgba(18, 18, 18, 0.08)), #F4F4F4;
	}

	&.active {
		visibility: visible;
		opacity: 1;

		+ .header-main .header__wrapper {
			box-shadow: none;
		}
	}
}

.card {
	margin-bottom: 20px;

	&.black {
		h2 {
			color: #222222;
		}

		p {
			color: rgba(#222222, .7);
		}

		a {
			color: #A467DE;
		}
	}
}

.card__content {
	h2 {
		color: #fff;
		margin-bottom: 4px;
		font-weight: 700;
	}

	p {
		color: #fff;
		margin-bottom: 0;
	}
}

.card__image {
	padding-bottom: 75.2%;
	background-repeat: no-repeat;
	background-position: 50% 50%;
	background-size: cover;
}

.hideUp {
	opacity: 0;
	visibility: hidden;
	transform:translate(0, 15px);
}

.hide {
	opacity: 0;
	visibility: hidden;
}

.fadeInUp {
	opacity: 1;
	visibility: visible;
	transform:translate(0, 0);
}

.fadeIn {
	opacity: 1;
	visibility: visible;
	transform:translate(0, 0);
}

.hero {
	// background: linear-gradient(0deg, rgba(255, 255, 255, 0.12), rgba(255, 255, 255, 0.12)), #121212;
	background-color: #2e2e2e;
	// height: 100vh;
	padding: 264px 0 395px;
	position: relative;
	overflow: hidden;

	.mountain1,
	.mountain2 {
		position: absolute;
		left: 0;
		bottom: 0;
		min-width: 100%;
	}

	.mountain1 {
		z-index: 2;
		height: 250px;
	}

	.mountain2 {
		z-index: 3;
		height: 180px;
	}

	.container {
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	.img-wrapper {
		transform:translate(-15% ,-19%);
		position: absolute;
		left: 50%;	
		bottom: 0;	

		&.active {
			.lottie-start {
				display: none;
			}

			.lottie-loop {
				display: block;
			}
		}
	}

	.lottie {
		width: 100%;
		height: auto; 
	}

	.lottie-start {
		display: block;
		width: 100%;
		height: auto;
	}

	.lottie-loop {
		display: none;
		width: 100%;
		height: auto;
	}

	article {
		width: 50%;
		position: relative;
		z-index: 5;
	}

	h1 {
		margin-bottom: 30px;
		transition: all 1s ease .2s;
		@extend .hideUp;
	}

	p {
		margin-bottom: 45px;
		transition: all 1s ease .4s;
		@extend .hideUp;
	}

	.btn-wrapper {
		display: flex;
		transition: transition 1s ease .6s, opacity 1s ease .6s, visibility 1s ease .6s;
		@extend .hideUp;
		
		a:not(:last-child) {
			margin-right: 30px;
		}
	}

	&.active {
		h1, p, .btn-wrapper {
			@extend .fadeInUp;
		}
	}

	@media(max-width: 1439px) and (min-width: 1025px) {
		padding: 244px 0 350px;

		.img-wrapper {
		    transform: translate(-15%, -17%);
		    width: 57%;
		}

		.mountain1 {
			height: 190px;
		}

		.mountain2 {
			height: 121px;
		}

		p {
			max-width: 380px;
		}
	}

	@media(max-width: 1024px) {
		padding-top: 156px;
		padding-bottom: 200px;

		h1 {
			margin-bottom: 20px;
		}

		p {
			max-width: 437px;
			margin: 0 auto;
		}

		.mountain1 {
			height: 147px;
		}

		.mountain2 {
			height: 106px;
		}


		.container {
			flex-direction: column;
			text-align: center;
			justify-content: center;

			article {
				max-width: 500px;
				width: auto;
			}

			br {
				display: none;
			}
		}

		.img-wrapper {
			position: relative;
			left: auto;
			bottom: auto;
			transform:translate(0 ,0);
			max-width: 360px;
			margin: 0 auto -112px;
		}

 
		.btn-wrapper {
			justify-content: flex-start;
			flex-direction: row-reverse;
			position: fixed;
			left: 0;
			visibility: visible!important;
			opacity: 1!important;
			transform: translate(0, 0);
			bottom: 0;
			padding: 20px 40px;
			background-color: #252525;
			width: 100%;
			// box-shadow: 0px 0px 8px rgba(18, 18, 18, 0.35);
			box-shadow: 0px -2px 10px rgba(0, 0, 0, 0.25);

			a {
				height: 36px;
				font-size: 14px;
				line-height: 19.6px;
				padding: 0 15px;
			}
			
			
			a:not(:last-child) {
				margin-left: 15px;
				margin-right: 0;
			}
		}
	}

	@media(max-width: 610px) { 
		padding-top: 126px;
		padding-bottom: 140px;

		h1 {
			margin-bottom: 15px;
		}

		p {
			font-size: 18px;
			line-height: 25.2px;
		}

		.container {
			padding: 0;
			max-width: initial;

			article {
				width: 100%;
				padding: 0 30px;
			}
		}

		.btn-wrapper {
			padding: 15px 20px;
		}

		.img-wrapper {
			transform:translate(0 ,0);
			max-width: 100%;
			margin: 0 auto -94px;
		}

		.mountain1 {
			height: 112px;
		}

		.mountain2 {
			height: 81px;
		}
	}

}

.steps {
	background: linear-gradient(0deg, rgba(255, 255, 255, 0.08), rgba(255, 255, 255, 0.08)), #121212;

	h2 {
		font-weight: 400;
	}

	.heading {
		transition: all 1s ease .2s;
		@extend .hideUp;
	}

	.img-wrapper {
		background: linear-gradient(0deg, rgba(255, 255, 255, 0.16), rgba(255, 255, 255, 0.16)), #121212;
		margin-top: 60px;
		transform: translate(-30px, 0);
		width: calc(100% + 60px);

		img {
			transition: all 1s ease .4s;
			@extend .hide;
		}
	}

	&.active {
		.heading {
			@extend.fadeInUp;
		}

		.img-wrapper {

			img {
				@extend.fadeIn;
			}

		}
	}
	@media(max-width: 1439px) and (min-width: 1025px) {
		.img-wrapper {
			transform: translate(0, 0);
			width: 100%;
		}
	}

	@media(max-width: 1024px) {
		text-align: center;

		h2 {
			max-width: 67%;
			margin: {
				left: auto;
				right: auto;
				bottom: 40px;
			}

			br {
				display: none;
			}
		}

		.img-wrapper {
			margin-top: 30px;
		}
	}

	@media(max-width: 610px) {
		padding-top: 0;

		h2 {
			max-width: 100%;
			padding: 0;
			margin: {
				bottom: 30px;
			}
		}

		.img-wrapper {
			transform: translate(-20px, 0);
			width: calc(100% + 40px);
		}
	}
}

.three-columns {
