}

.tab-section {
	padding: 140px 0 138px;
	background-color: #EAEAEA;

	h2 {
		margin-bottom: 62px;
	}

	.heading {
		transition: all 1s ease .4s;
		@extend .hideUp;
	}

	.tab-wrapper {
		transition: all 1s ease .8s;
		@extend .hide;

		&:hover {
			.tab-trigger {
				&.is-active {

					&:before {
						// animation: none;
						animation-play-state: paused;
						// animation: autoLoad 10s linear;
					}
					
				}
			}
		}
	}

	.tab-list {
		display: flex;
		width: 100%;
		margin-bottom: 0;

		li {
			flex:1;

			&:last-child {
				display: none;
			}
		}
	}


	.tab-trigger {
		height: 58px;
		display: flex;
		align-items: center;
		justify-content: center;
		color: rgba(#333, .4);
		border-bottom: 4px solid rgba(18, 18, 18, 0.18);
		transition: all .3s ease;
		font-weight: 700;
		white-space: nowrap;
		padding: 0 10px;
		position: relative;

		&:before {
			position: absolute;
			left: 0;
			bottom: -4px;
			height: 4px;
			background-color: #7315CD; 
			width: 0;
			content:"";
			z-index: 5;
		}

		&.is-active {
			color: #7315CD;

			&:before {
				// width: 100%;
				animation: autoLoad 4s linear;

			}
			
		}
	}

	&.active {
		.heading {
			@extend.fadeInUp;
		}

		.tab-wrapper {
			@extend.fadeIn;
		}
		
	}

	@media(max-width: 1439px) and (min-width: 1025px) {
		padding: 120px 0;

		h2 {
			margin-bottom: 42px;
		}
	}

	@media(max-width: 1024px) {
		padding: 100px 0;

		h2 {
			margin-bottom: 40px;
			text-align: center;
		}

		.tab-trigger {
			height: 48px;
			font-size: 14px;
			border:none;

			&:before {
				bottom: 0;
				height: 2px;
			}
		}
	}

	@media(min-width: 611px) {
		h2 {
			br {
				display: none!important;
			}
		}
	}

	@media(max-width: 610px) {
		background-color: #EAEAEA;
		padding: 80px 0 40px;

		h2 {
			margin-bottom: 20px;
		}

		h3 {
			text-align: left!important;
		}

		p {
			font-size: 18px!important;
		}

		.tab-list-wrapper {
			position: relative;
			transform:translate(-30px, 0);
			width: calc(100% + 60px);

			&:before {
				content: "";
				position: absolute;
				background: linear-gradient(90deg, rgba(234,234,234,1) 0%, rgba(255,255,255,0) 100%); 
				left: 0;
				top: 0;
				height: 100%;
				width: 60px;
				z-index: 5;
			}

			&:after {
				content: ""; 
				position: absolute;
				background: linear-gradient(270deg, rgba(234,234,234,1) 0%, rgba(255,255,255,0) 100%);
				right: 0;
				top: 0;
				height: 100%;
				width: 60px;
				z-index: 5;
			}

		}


		.tab-list {
			flex-direction: row;
			overflow-x: auto;
			overflow-y: hidden; 
			scrollbar-width: none; /* Firefox */
			-ms-overflow-style: none;  /* IE 10+ */
			&::-webkit-scrollbar {
				width: 0px;
				background: transparent; /* Chrome/Safari/Webkit */
			}

			li {
				width: 100%;
				&:first-child {
					margin-left: 30px;
				}
				&:nth-child(4) {
				}
				&:last-child {
					padding: 0 15px;
					display: block;
				}
			}

		}
		.tab-trigger {
			border-bottom: 2px solid rgba(18, 18, 18, 0.18);
			padding: 0 15px;

			&:before {
				bottom: -2px;
				height: 2px;
			}
		}

		.tab-content__list {
			li {
				padding-left: 20px;

				&:before {
					top: 10px;
					width: 8px;
					height: 8px;
				}
			}
		}

	}
}

