			max-width: 50%;
			padding: 0 15px;
			font-size: 14px;
		}
		.input-box {
			justify-content: space-between;
			max-width: 100%;
		}

		form {
			margin-top: 15px;
		}

		input {
			height: 36px!important;

		}

		button {
			font-size: 14px;
			padding: 0 15px;
			height: 36px!important;
			width: auto;
			span {
				display: none;
			}
		}

		.text-small {
			font-size: 14px!important;
			line-height: 19.6px!important;
		}
	}

	

	@media(min-width: 1024px) {
		h3 {
			br {
				display: none!important;
			}
		}

	}

	@media(max-width: 610px) {
		flex-direction: column-reverse;
		
		.subscribe-box__content {
			padding: 30px 20px;
			width: 100%;
			background-image: url('../images/dark-bgr1.png');

			p {
				max-width: 200px;
			}
		}

		.subscribe-box__img {
			min-width: 100%;
			padding-bottom: 55%; 
			transform: translate(0, 0);
			margin-bottom: -60px;
			background-position: right bottom;
			background-size: contain;

		}

		form {
			margin-top: 20px;

			.input {
				max-width: calc(100% - 87px);
				font-size: 12px;
			}
		}

		h3 {
			font-size: 24px;
			line-height: 30px;
		}
	}
}

.three-column-box {
	background-color: #f4f4f4;
	padding: 140px 0 60px;

	.card {
		background-color: #fff;
		padding: 30px;
		position: relative;

		.card__img-wrapper {
			width: 80px;
			margin-bottom: 8px;
		}

		h2 {
			font-weight: 700;
			margin-bottom: 8px;
		}

		p {
			margin-bottom: 56px;
		}

		a {
			font-weight: 700;

			&:hover {
				text-decoration: underline;
			}
		}


	}

	.card1 {
		transition: all 1s ease .4s;
		@extend .hideUp;
	}

	.card2 {
		transition: all 1s ease .6s;
		@extend .hideUp;
	}

	.card3 {
		transition: all 1s ease .8s;
		@extend .hideUp;
	}

	.card__content {

		a {
			white-space: nowrap;
			position: absolute;
			left: 30px;
			bottom: 30px;
		}
	}

	&.active {
		.card {
			@extend.fadeInUp;
		}
	}

	@media(max-width: 1439px) and (min-width: 1025px) {
		padding: 120px 0 100px;
	}

	@media(max-width: 1024px) {
		padding: 100px 0 60px;
 
		.card__img-wrapper {
			margin: 0 0 8px;
			max-width: 64px;
		}

		h2 {
			margin-bottom: 0!important;
		}

		p {
			margin-bottom: 36px!important;

			br {
				display: none;
			}
		}

		a {
			font-size: 14px;
			line-height: 19.6px;
		}

		.card {
			padding: 20px;
			height: 100%;
			margin-bottom: 0;
		}

		.card__content {

			a {
				bottom: 20px;
				left: 20px;
			}
		}
	}


	@media(max-width: 610px) {
		padding: 60px 0 40px;

		.row {
			>div {
				margin-bottom: 10px;
			}
		}

		p {
			max-width: 205px;
		}

		.card {
			padding: 20px 15px;
			display: flex;
			flex-direction: row-reverse;
			justify-content: space-between;
			position: relative;

			.card__content {
				width: 100%;

				a {
					left: 15px;
				}
			}

			.card__img-wrapper {
				margin-left: 15px;
				max-width: 60px;
				position: absolute;
				top: 17px;
				right: 7px;
			}
		}
	}
}