/**
* Reset input styles.
*/
.input {
  -webkit-appearance: none;
  /* removing inner shadow on iOS inputs */
  -webkit-font-smoothing: inherit;
  box-sizing: border-box;
  transition: all .5s ease;
  background: none;
  border: none;
  color: $color-base;
  width: 100%;
  display: inline-block;
  font-family: $base-font-family;
  font-size: $base-font-size;
  padding: 0;
  /* remove browser defaults */
  vertical-align: middle;
  width: 100%;

  &:invalid {
    box-shadow: none;
    /* fixes mozilla red outline (shadow) */
  }

  &[readonly] {
    cursor: text;
  }

  &::-ms-clear {
    display: none;
    /* remove IE clear icon */
  }

  @mixin input-placeholder {
    color: $color-base;
  }

  &:not(:disabled) {

    &:hover {}

    &:focus {
      outline: none;
    }
  }
}

.input--primary {
  height: $input-height;
  padding: 0 $input-padding;
  color: $color-input;
  display: flex;
  background: #FFFFFF;
  border: 2px solid rgba(18, 18, 18, 0.1);
  height: 60px;

  &:not(:disabled) {

    &:hover {}

    &:focus {}

    @mixin input-placeholder {
      color: $color-input-placeholder;
    }
  }

  /**
   * @disabled
   */
  &:disabled {
    opacity: 0.5;
    cursor: not-allowed !important;
  }

  &.error {
    color: #BE2B2B!important;
    border-color: #BE2B2B!important;
  }
}

.input--textarea {
    resize: none;
    min-height: 140px;
    padding: 20px;
}

.input-box {
  margin-bottom: 20px;
  position: relative;
}

.input-box--button {
  button {
    position: absolute;
    z-index: 2;
    right: 8px;
    top: 8px;
    height: 44px;
  }
}

span.error {
  color: #BE2B2B;
  display: flex;
  align-items: center;
  margin-top: 12px;

  svg {
    margin-right: 10px;
  }
}




.check-box {
    position: relative;
    input[type="checkbox"] {
        visibility: hidden;
        opacity: 0;
        position: absolute;
        z-index: -1;
    }
  
    label {
      display:flex;
    }

    label.error {
      position: absolute;
      left: 0;
      top: 100%;
      margin-top: 5px;
      font-size: 10px;
      color: red;
    }
}

.check-box {
    // vertical-align: middle;
    margin-bottom: 15px;

    .check-icon {
        min-width: 18px;
        height: 18px;
        border: 2px solid rgba(51, 51, 51, 0.35);
        background-color: #fff;
        margin-right: 10px;
        position: relative;
        transition: all .3s ease;
        margin-top: 5px;
        display: flex;
        align-items: center;
        justify-content: center;

        svg {
            transition: all .3s ease;
            visibility: hidden;
            opacity: 0;
        }

    }
    label {
        font-weight: 400;
        cursor: pointer;
    }

    input[type="checkbox"]:checked + label {
        .check-icon {
            background-color: #7315CD;
            border-color: #7315CD;

            svg {
                visibility: visible;
                opacity: 1;
            }
        }
    }

    input[type="checkbox"]:disabled + label {
        pointer-events:none;
        opacity: .8;
        cursor: not-allowed;
    }
    .check-text {
      margin-bottom: 0;
      font-size: 16px;
      line-height: 25px;
      color: #555555;
    }
}

.check-box--input {
  label {
    width: 100%;
  }
}

.check-box {
  a {
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }
  }
}

.wpcf7-form .wpcf7-response-output.wpcf7-validation-errors {
    color: #D8000C;
    background-color: #FFBABA;
    border: 0;
    padding: 10px;
}
.wpcf7-form .wpcf7-response-output.wpcf7-mail-sent-ok {
    color: #4F8A10;
    background-color: #DFF2BF;
    border: 0;
    padding: 10px;
}
div.wpcf7-mail-sent-ok:before,div.wpcf7-validation-errors:before {
    font: 26px/30px FontAwesome;
    margin-right: 16px;
    vertical-align: middle;
}
div.wpcf7-mail-sent-ok:before {
    content:'\f00c';
}
div.wpcf7-validation-errors:before {
    content:'\f057';
}