/* ------------------------------------*\
    #HEADINGS
\*------------------------------------ */

h1,
h2,
h3,
h4,
h5,
h6 {
  >a {
    color: inherit;
  }
}

h1,
.h1 {
	font-size: 64px;
	line-height: 70.4px;

	@media(max-width: 1440px) {
		font-size: 48px;
		line-height: 52.8px;
	}

	@media(max-width: 610px) {
		font-size: 32px;
		line-height: 35.2px;
	}
}

h2,
.h2 {
	font-size: 36px;
	line-height: 45px;
	color: #333;

	@media(max-width: 1440px) {
		font-size: 32px;
		line-height: 40px;
	}

	@media(max-width: 610px) {
		font-size: 24px;
		line-height: 30px;
	}
}

h3,
.h3 {
	font-size: 24px;
	line-height: 33.6px;

	@media(max-width: 1440px) {
		font-size: 18px;
		line-height: 25.2px;
	}
}

h4,
.h4 {
	font-size: 16px;
	line-height: 25.6px;
}

h5,
.h5 {}

h6,
.h6 {}
