.hero--about {
	padding: 224px 0 0;

	.img-wrapper {
		width: 100%;
		height: auto;
		margin-bottom: -2px;

		img {
			width: 100%;
		}

		.lottie {
			width: 100%;
			position: relative;
			display: block;

			>div {
				transform: none!important;
				width: 100%!important;
			}
		}
	}

	h1 {
		margin-bottom: 20px;
	}
	p {
		margin-bottom: 0;
	}

	@media(max-width: 1024px) {
		padding: 186px 0 0;

		article {
			max-width: 570px!important;
		}

		.img-wrapper {
			transform: translate(0, 0);
		}
