.who {
	background: #EAEAEA;
	padding: 60px 0;

	article {
		position: relative;
		background: linear-gradient(0deg, rgba(255, 255, 255, 0.16), rgba(255, 255, 255, 0.16)), #121212;
		padding: 60px;
		display: flex;
		justify-content: space-between;
	}

	.who-content__text {
		width: 53%;

		h3 {
			margin-bottom: 30px;
			transition: all 1s ease .2s;
			@extend .hideUp;
		}
	}

	.who__img {
		width: 42%;
		height: 100%;

		.img-wrapper {
			background-repeat: no-repeat;
			background-position: 50% 50%;
			background-size: cover;
			padding-bottom: 100%;
			width: 100%;
			transition: all 1s ease 1s;
			@extend .hide;
		}

	}

	.who__list {
		margin-bottom: 0;

		li {
			padding-left: 30px;
			position: relative;
			@extend .hideUp;


			&:before {
				position: absolute;
				left: 0;
				top: 12px;
				content:"";
				width: 12px;
				height: 12px;
				background-color: #A467DE;

				@media(max-width: 1024px) {
					width: 8px;
					height: 8px;
				}
			}

			&:not(:last-child) {
				margin-bottom: 30px;
			}

			@media(max-width: 1024px) {
				padding-left: 23px;

				&:not(:last-child) {
					margin-bottom: 20px;
				}

				&:before {
					top: 10px;
				}
			}

			&:last-child {

				p {
					margin-bottom: 0;
				}
			}

			&:nth-child(1) {
				transition: all 1s ease .4s;

			}
			&:nth-child(2) {
				transition: all 1s ease .6s;

			}
			&:nth-child(3) {
				transition: all 1s ease .8s;

			}
		}
	}

	&.active {
		h3, li, .img-wrapper {
			@extend .fadeInUp;
		}
	}

	@media(max-width: 1439px) and (min-width: 1025px) {
		padding: 50px 0;

		article {
			padding: 50px;

			.who-content__text {
				h3 {
					margin-bottom: 20px;
				}
			}

			.who__img {
				width: 36%;
			}
		}
	}	

	@media(max-width: 1024px) {

		article {
			flex-direction: column;
			padding: 60px 110px;
		}

		.who-content__text {
			h3 {
				text-align: center;
				margin-bottom: 20px;
			}
		}

		.who-content__text, 
		.who__img {
			width: 100%;
		}

		.who__img {
			margin-top: 30px;

			.img-wrapper {
				padding-bottom: 53%;
			}
		}
	}

	@media(max-width: 610px) {
		padding: 30px 0;

		article {
			padding: 30px 20px;
		}

		.who__img {
			margin-top: 20px;

			.img-wrapper {
				padding-bottom: 66%;
			}
		}
	}
}

.team-three-column {
	padding: 140px 0;
	background: #EAEAEA;

	h2 {
		@extend .hideUp;
		transition: all 1s ease;
	}

	.row {
		>div {
			margin-bottom: 30px;
			@extend .hide;

			&:nth-child(1) {
				transition: all 1s ease .2s;
			}
			&:nth-child(2) {
				transition: all 1s ease .4s;
			}
			&:nth-child(3) {
				transition: all 1s ease .6s;
			}
			&:nth-child(4) {
				transition: all 1s ease .8s;
			}
			&:nth-child(5) {
				transition: all 1s ease 1s;
			}
			&:nth-child(6) {
				transition: all 1s ease 1.2s;
			}
		}
	}

	&.active {
		h2 {
			@extend .fadeInUp;
		}
		.row {
			>div {
				@extend .fadeIn;
			}
		}
	}

	h2 {
		margin-bottom: 60px;
	}

	h3 {
		margin-bottom: 0;
		color: #333333;
	}

	h4 {
		margin-bottom: 10px;
		color: #333333;
	}

	.card {
		background-color: #fff;
		margin-bottom: 0;
	}

	.social-list {
		display: flex;
		align-items: center;
		margin-left: 0;
		margin-bottom: 0;

		li {
			margin-right: 10px;
		}

		a {
			color: #7315CD;
			height: 44px;
			width: 44px;
			display: flex;
			align-items: center;
			justify-content: center;
			font-size: 25px;

			&:hover {
				color: darken(#7315CD, 10%);
			}
		}
	}

	.card__content {
		display: flex;
		flex-direction: column;
		padding: 30px;
	}

	.card__image-wrapper {

		.social-list {
			display: none;
		}
	}

	.card__image {
		margin-bottom: 20px;
	}

	.card__text {

		p {
			color: rgba(51, 51, 51, 0.7);
			margin-bottom: 20px;
		}

		.social-list {
			display: flex;
		}
	}

	@media(max-width: 1439px) and (min-width: 1025px) {
		padding: 120px 0 ;

		.button {
			font-size: 14px;
			line-height: 19.6px;
			height: 44px;
		}
	}

	@media(max-width: 1024px) {
		padding: 100px 0 ;

		h2 {
			margin-bottom: 80px;
			font-size: 48px;
			line-height: 52.8px;
		}

		.card__content {
			padding: 15px;
		}

		.card--image {
			min-height: 468px;
			position: relative;
		}

		.card__text {

			.social-list {
				position: absolute;
				left: 15px;
				bottom: 15px;
			}
		}

	}

	@media(max-width: 991px) and (min-width: 611px) {
		.card__text p {
			margin-bottom: 50px;
		}
	}

	@media(max-width: 610px) {
		padding: 80px 0;

		br {
			display: none;
		}

		.row {
			>div {
				margin-bottom: 30px;
				flex-basis: 100%;
				max-width: 100%;
			}
		}


		.card {
			margin-bottom: 0;	
		}

		.item {
			& + .item {
				margin-top: 30px;	
			}
		}

		.heading {
			font-size: 32px;
			line-height: 35.2px;
			margin-bottom: 30px;
		}

		.card__text,h3, h4 {
			padding: 0;	
		}

		.card__content {
			padding: 15px;
		}

		.card__image-wrapper {
			overflow: hidden;

			.social-list {
				display: flex;
			}

			svg {
				width: 24px;
			}

			h3 {
				margin-bottom: 5px;
				margin-top: 12px;
			}

			h4 {
				font-size: 14px;
				line-height: 22.4px;
				margin-bottom: 0;
			}
		}

		.card__image {
			width: 100px;
			height: 100px;
			padding: 0;
			float: left;
			margin-right: 10px;
			margin-bottom: 10px;

		}

		.card--image {
			min-height: 0;
		}

		.card__text {

			p {
				margin-bottom: 0;
				font-size: 14px;
				line-height: 22.4px;
			}
			.social-list {
				display: none;
			}
		}

		.button  {
			margin-top: 0!important;
		}
	}
}

