.groups-associations {
	background: #F4F4F4;
	padding: 140px 0 100px;

	h2 {
		margin-bottom: 80px;
		@extend .hideUp;
		transition: all 1s ease;

	}

	.row {
		margin-left: -15px;
		margin-right: -15px;

		>div {
			padding: 0 15px;
			margin-bottom: 40px;
			@extend .hide;

			&:nth-child(1) {
				transition: all 1s ease .2s;
			}
			&:nth-child(2) {
				transition: all 1s ease .4s;
			}
			&:nth-child(3) {
				transition: all 1s ease .6s;
			}
			&:nth-child(4) {
				transition: all 1s ease .8s;
			}
			&:nth-child(5) {
				transition: all 1s ease 1s;
			}
			&:nth-child(6) {
				transition: all 1s ease 1.2s;
			}
			&:nth-child(7) {
				transition: all 1s ease 1.4s;
			}
			&:nth-child(8) {
				transition: all 1s ease 1.6s;
			}

		}
	}

	&.active {

		h2 {
			@extend .fadeInUp;
		}

		.row {
			>div {
				@extend .fadeIn;
			}
		}
	}

	.item {
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: #fff;
		width: 100%;
		height: 100%;
	}

	@media(max-width: 1439px) and (min-width: 1025px) {
		padding: 120px 0 90px;

		h2 {
			margin-bottom: 50px;
		}
	}

	@media(max-width: 1024px) {
		padding: 100px 0 70px;

		h2 {
			margin-bottom: 60px;
		}

		.row {

			>div {
				margin-bottom: 30px;
			}
		}
	}

	@media(max-width: 610px) {
		padding: 80px 0 60px;

		h2 {
			margin-bottom: 40px;
		}

		.row {
			margin-left: -7.5px;
			margin-right: -7.5px;

			>div {
				padding: 0 7.5px;
				margin-bottom: 15px;
			}
		}
	}
}