@keyframes autoLoad {
  	0%  { 
  		width: 0;
  		
	}

  	100% { 
  		width: 100%; 
  	}
}

@keyframes waveOne {
  	0%  { 
  		width: 40%;
  		height: 40%;
  		visibility: visible;
		opacity: 1;
 	}
 	99% { 
  		width: 100%; 
  		height: 100%;
 	}
  	100% { 
  		width: 100%; 
  		height: 100%;
  		opacity: 0;
  	}
}

@keyframes waveTwo {
  	0%  { 
  		width: 40%;
  		height: 40%;
		visibility: visible;
		opacity: 1;
 	}
 	99% { 
  		width: 70%; 
  		height: 70%;
 	}
  	100% { 
  		width: 70%; 
  		height: 70%;
  		opacity: 0;
  	}
}

@keyframes under-item-animate {
  	0%  { 
  		transform:translate(-50%, 10px);
 	}

  	10% { 
  		transform:translate(-50%, 30px);
  	}

  	90% { 
  		transform:translate(-50%, 30px);
  	}

  	100% { 
  		transform:translate(-50%, 10px);
  	}
}

@keyframes changeColorCloudElement3 {
  	0%  { 
  		fill: #A467DE;
 	}

 	10%  { 
  		fill: lighten(#424242,15%);
 	}

 	80%  { 
  		fill: lighten(#424242,15%);
 	}

  	90% { 
  		fill: #A467DE;
  	}

}


@keyframes buttonBoxVisibleLine {
  	0%  { 
		visibility: hidden;
		opacity: 0;
 	}
 	5% { 
  		visibility: visible;
		opacity: 1;
  	}
	95% { 
  		visibility: visible;
		opacity: 1;
 	}
  	100% { 
  		visibility: hidden;
		opacity: 0;
  	}
}

@keyframes buttonBoxComet {
  	0%  { 
		transform: translate(-50%, -100%);
		background: linear-gradient(180deg, rgba(164,103,222,0) 0%, rgba(164,103,222,.8) 100%);
 	}
 	10% { 
  		transform: translate(-50%, 200%);
		background: linear-gradient(180deg, rgba(164,103,222,0) 0%, rgba(164,103,222,.8) 100%);
  	}
	70% { 
  		transform: translate(-50%, 200%);
		background: linear-gradient(180deg, rgba(164,103,222,0) 0%, rgba(164,103,222,.8) 100%);
 	}
  	95% { 
  		transform: translate(-50%, -100%);
		background: linear-gradient(0deg, rgba(164,103,222,0) 0%, rgba(164,103,222,1) 100%);
  	}

  	100% { 
  		transform: translate(-50%, -100%);
		background: linear-gradient(0deg, rgba(164,103,222,0) 0%, rgba(164,103,222,1) 100%);
  	}
}

@keyframes cloudElement4Border {
  	0%  { 
		stroke-dashoffset: 1000;
 	}
 	10% { 
  		stroke-dashoffset: 0;
  	}

  	80% { 
  		stroke-dashoffset: 1000;
  	}

  	100% { 
  		stroke-dashoffset: 1000;
  	}
}

@keyframes moveTop {
  	0%  { 
		transform:translate(0, 0);
 	}
 	10% { 
  		transform:translate(0, 20px);
  	}

  	90% { 
  		transform:translate(0, 20px);
  	}

  	100% { 
  		transform:translate(0, 0);
  	}
}