/* ------------------------------------*\
    #HEADER HOMEPAGE
\*------------------------------------ */

.header-main {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	z-index: 999;
	margin-top: 60px;

	&.light-dark {

		.header__wrapper {

			&:before {
				transition: all .1s ease;
				background-color: #2e2e2e;
			} 
		}
	}

	&.dark {

		.header__wrapper {

			&:before {
				transition: all .1s ease;
				background: linear-gradient(0deg, rgba(255, 255, 255, 0.08), rgba(255, 255, 255, 0.08)), #121212;
			} 
		}

	}

	&.gray {

		.header__wrapper {

			&:before {
				transition: all .1s ease;
				background-color: #EAEAEA;
			} 
		}

	}

	&.light-gray {

		.header__wrapper {

			&:before {
				transition: all .1s ease;
				background-color: #f4f4f4;
			} 
		}
	} 

	&.light-gray-2 {

		.header__wrapper {

			&:before {
				transition: all .1s ease;
				background: linear-gradient(0deg, rgba(18, 18, 18, 0.06), rgba(18, 18, 18, 0.06)), #F4F4F4;
			} 
		} 
	} 

	&.dark-blue {

		.header__wrapper {

			&:before {
				transition: all .1s ease;
				background: linear-gradient(0deg, rgba(255, 255, 255, 0.04), rgba(255, 255, 255, 0.04)), #181E29;
			} 
		}
	} 

	&.dark-blue-2 {

		.header__wrapper {

			&:before {
				transition: all .1s ease;
				background: #181D29;
			} 
		}
	} 

	

	&.active {
		height: 60px;
	}

	&.toFixed {
		position: fixed;
		transform: translate(0, -100%);
		margin-top: 0;
	}

	&.slideTop {
		transform: translate(0, 15px);
		transition: transform .3s ease;
		margin-top: 0;

		.header__wrapper {
			box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.25);
		}

		.main-nav__dropdown {
			box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.25);
		}
	}

	&.slideBottom {
		transition: transform .3s ease;
		transform: translate(0, -100%);
		margin-top: 0;
	}

	&.is-active {

		&.toFixed {
			transform:translate(0, 15px)!important;
		}
	}

	@media (min-width: 768px) {

		&.toFixed {
			.main-nav__dropdown {
				box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.25);
			}
		}
	}

	@media(max-width: 1439px) and (min-width: 1025px) {
		margin-top: 40px;

		.container {
			max-width: 970px!important;
			padding: 0;

			.header__wrapper {
				padding: 0 20px 0 25px;
			}
		}
	}

	@media (max-width: 1024px) {
		margin-top: 30px;

		.container {
			padding: 0 20px;
		}
	}

	nav {
		@media (max-width: 768px) {
			position: absolute;
			background: #383838;
			top: 100%;
			left: 0;
			width: 100%;
			padding: 20px 15px;
			visibility: hidden;
			opacity: 0;
			transition: all .3s ease;
			height: auto;
			overflow-y: auto;
			overflow-x: hidden;
			height: calc(100vh - 86px);
			
			&.active {
				visibility: visible;
				opacity: 1;
			}
		}
	}

	.container {
		max-width: 1270px;
	}

	.mobile-heading {
		p {
			margin-bottom: 0;
		}
	}

	.mobile-links,
	.mobile-heading {
		display: none;
	}
	

	@media (max-width: 768px) {
		// transform:translate(0, 15px);
		margin-top: 15px;
		padding: 0 15px;
		position: fixed;
		transform:translate(0,0)!important;
		margin-top: 15px!important;

		.desktop-links {
			display: none!important;
		}

		.mobile-links, 
		.mobile-heading {
			display: block;
		}

		.mobile-links {
			flex: 0 0 50%;
			margin-bottom: 0!important;
		}

		.container {
			padding: 0;
		}

		.sign-in {
			width: 100%!important;
			margin-top: 75px!important;

			.button {
				width: 100%!important;
			}
		}
	}
}

.header__wrapper {
	background: linear-gradient(0deg, rgba(255, 255, 255, 0.16), rgba(255, 255, 255, 0.16)), #121212;
	height: 84px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 0 30px 0 35px;
	position: relative;

	&:before {
		position: absolute;
		left: 0;
		bottom: 100%;
		height: 15px;
		width: 100%;
		content:"";
	}

	&:after {
		position: absolute;
		left: 0;
		top: 100%;
		height: 15px;
		width: 100%;
		content:"";
		visibility: hidden;
		opacity: 0;
		transition: all .5s ease;
		background: linear-gradient(0deg, rgba(255, 255, 255, 0.14), rgba(255, 255, 255, 0.14)), #121212
	}

	&.is-active {
		&:after {
			visibility: visible;
			opacity: 1;

		}
	}


	@media (max-width: 1024px) {
		padding: 0 20px 0 25px;
		height: 66px;

		&.active {
			&:before {
				display: none;
			}
			&:after {
				display: none;
			}
		}

		.logo {

			svg {
				width: 68px;
			}
		}
	}

	@media (max-width: 768px) {
		background-image: none;
		background-color: #383838;
		padding: 0 15px;
		height: 56px;


		.logo {
			svg {
				width: 56px;
			}
		}
	}
}

nav {
	height: 100%;
}

.main-nav {
	margin-bottom: 0;
	display: flex;
	align-items: center;
	height: 100%;

	li {
		height: 100%;

		& + li:not(:last-child) {
			margin-left: 20px;

			@media(max-width: 1439px) and (min-width: 1025px) {
				margin-left: 10px;
			}

			@media(max-width: 1024px) {
				font-size: 14px;
				margin-left: 10px;
			}

			@media (max-width: 768px) {
				margin-left: 0;
				margin-bottom: 10px;
			}
		}

		&:last-child {
			margin-left: 40px;

			@media(max-width: 1439px) and (min-width: 1025px) {
				margin-left: 20px;
			}

			@media(max-width: 1024px) {
				margin-left: 25px;
			}
		}
	}



	@media (max-width: 768px) {
		align-items: flex-start;
		flex-wrap: wrap;
		margin-bottom: 25px;
		height: auto;
	
		li {
			height: auto;
			width: 100%;

			&:first-child {
				a {
					svg {
						display: none;
					}
				}
			}	

			&:last-child {
				margin-left: 0;
				margin-top: 10px;
			}
		}
	}
}


.main-nav__link:not(.button) {
	color: #fff;
	padding: 10px 20px;
	font-weight: 700;
	font-size: 16px;
	line-height: 22.4px;
	opacity: .7;
	display: flex;
	align-items: center;
	position: relative;

	@media(max-width: 1024px) {
		font-size: 14px;
		line-height: 22.4px;
		font-weight: 700;
		padding: 10px 15px;
	}

	@media(min-width: 768px) {
		height: 100%;
	}

	@media(max-width: 768px) {
		padding: 10px 0;
		opacity: 1;
		font-size: 16px;
		line-height: 22.4px;
		font-weight: 600;
	}

	&:hover,
	&:focus {
		opacity: 1;
		color: #fff;

		svg {
			path {
				stroke-opacity: 1;
			}
		}
	}
}

.main-nav__link-mob {

	svg {
		margin-left: 5px;
		margin-top: 3px;
	}

	&.is-active {

		color: #A467DE!important;

		path {
			stroke: #A467DE!important;
			stroke-opacity: 1;
		}

		svg {
			transform: rotate(180deg);
		}
	}

	@media(max-width: 768px) {
		pointer-events:none;
	}
}

.main-nav__link.button {
	font-weight: 700;
	height: 42px;

	@media(max-width: 1024px) {
		font-size: 14px!important;
		height: 36px!important;
		padding: 0 15px!important;
	}
}

.main-nav__trigger {

	&:hover {

		> .main-nav__link {
			opacity: 1;
			color: #fff;

			svg {
				path {
					stroke-opacity: 1;
				}
			}
		}
	}
}

.main-nav__dropdown {
	position: absolute;
	left: 0;
	top: 100%;
	width: 100%;
	background: linear-gradient(0deg, rgba(255, 255, 255, 0.14), rgba(255, 255, 255, 0.14)), #121212;
	padding: 60px 40px;

	@media(min-width: 769px) {
		z-index: -1;
		visibility: hidden;
		opacity: 0;
		transform: translate(0,-40px);
		transition: all .3s ease;

		&.is-active {
			opacity: 1;
			visibility: visible;
			transform: translate(0,0);
		}
	}

	@media(max-width: 768px) {
		box-shadow: none!important;
	}

	.container {
		display: flex;
		justify-content: space-between;

		@media(max-width: 1200px) {
		    padding: 0;

		    .card {
		    	padding: 0 12px;
		    }
	  	}

	  	@media(max-width: 1024px) {
	  		br {
	  			display: none;
	  		}

	  		.card__img-wrapper {
	  			margin-bottom: 10px;
	  		}
	  	}

	  	@media(max-width: 768px) {
		    padding: 0;

		    .card {
		    	padding: 0;
		    }

		    .card__img-wrapper {
	  			margin-bottom: 0;
	  		}
	  	}
	}

	.card__img-wrapper {
		margin-bottom: 20px;

		img {
			width: 72px;
		}
	}

	.card {
		margin-bottom: 0;

		h2 {

			span {
				font-size: 14px;
				font-weight: 600;
			}
		}

		p {
			opacity: .7;
		}


		&.disabled {
			h2 {
				opacity: .35;
			}

			p {
				color: rgba(255, 255, 255, 0.25);
				opacity: 0.6;
			}
		}
	}

	@media (max-width: 1024px) {
		padding: 30px 20px;

		h2 {
			font-size: 14px;
			line-height: 19.6px;

			span {
				font-size: 10px!important;
				line-height: 12px;
				font-weight: 700;
			}
		}
	}

	@media (max-width: 768px) {
		position: relative;
		left: 0;
		top: 0;
		display: block;
		background-image: none!important;
		background-color: transparent!important;
		padding: 10px 0;

		h2 {
			font-size: 16px;
			line-height: 22.4px;
			font-weight: 600!important;
		}

		p {
			font-size: 12px;
			line-height: 16.8px;
		}

		.container {
			display: flex;
			flex-direction: column;
			justify-content: center;
		}

		br {
			display: none;
		}

		.card {
			margin-bottom: 30px;
			display: flex;
		}

		.card__img-wrapper {
			margin-bottom: 10px;
			min-width: 72px;
			max-width: 72px;
			margin-right: 15px;
		}
	}
}
.bars{
	width: 25px;
	height: 20px;
	margin-top: 6px;
	position: relative;
	transform: rotate(0deg);
	transition: .5s ease-in-out;
	cursor: pointer;
	display: none;

	&:hover,
	&:focus {

		span {
			background: #A467DE;
		}
	}
	span { 
		display: block;
		position: absolute;
		height: 2px;
		width: 100%;
		background: #fff;
		border-radius: 2px;
		opacity: 1;
		left: 0;
		transform: rotate(0deg);
		transition: .25s ease-in-out;
	}
	span:nth-child(1) {
	  top: 0px;
	}

	span:nth-child(2),span:nth-child(3) {
	  top: 6px;
	}

	span:nth-child(4) {
	  top: 12px;
	}

	&.active span:nth-child(1) {
	  top: 10px;
	  width: 0%;
	  left: 50%;
	}

	&.active span:nth-child(2) {
	  transform: rotate(45deg);
	}

	&.active span:nth-child(3) {
	  transform: rotate(-45deg);
	}

	&.active span:nth-child(4) {
	  top: 10px;
	  width: 0%;
	  left: 50%;
	}

	@media (max-width: 768px) {

		display: block;
	}
}


/* ------------------------------------*\
    #HEADER KRANE
\*------------------------------------ */



.header-main--krane {
	.header__wrapper {
		background: linear-gradient(0deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.1)), #181E29;

		&:after {
			background: #2a303a;
		}

		.main-nav__dropdown {

			background: #2a303a;
		}
	}

	.main-nav__link-mob {

		&.is-active {

			color: #fff!important;
 
			path {
				stroke: #fff!important;
			}
		}
	}

	&.light-dark {

		.header__wrapper {

			&:before {
				background: #1A1F2B;
			} 
		}
	} 

	nav {
		@media (max-width: 768px) {
			background: #2f343e;
		}
	}
	@media(max-width: 1024px) {
		.logo {

			svg {
				width: 60px;
			}
		}
	}
	@media (max-width: 768px) {

		.logo {

			svg {
				width: 56px;
			}
		}

		.bars{

			&:hover,
			&:focus {

				span {
					background: #679BFF;
				}
			}
		}
	}
}

/* ------------------------------------*\
    #HEADER LIGHT
\*------------------------------------ */

.header-main--light {
	.header__wrapper {
		background: linear-gradient(0deg, rgba(18, 18, 18, 0.08), rgba(18, 18, 18, 0.08)), #F4F4F4;

		&:after {
			background-color: #ebebeb;

		}
	}

	.logo {
		path {
			fill: #7315CD;
		}
	}

	.main-nav__link {
		color: rgba(51, 51, 51, 0.7);
		font-weight: 700;

		&:focus {
			color: rgba(51, 51, 51, 0.7);
		}

		&:hover {
			color: #7315CD;
		}

		&.active {
			color: #7315CD;
			opacity: 1;
		}
	}

	.main-nav__trigger {

		&:hover {

			.main-nav__link {
				color: rgba(51, 51, 51, 0.7);
			}
		}

		h2, p {
			color: #121212!important;
		}
	}

	.main-nav__link.button {
		@extend.button--dark-purple-transparent;
	}

	.main-nav__link-mob {

		path {
			stroke: rgba(51, 51, 51, 0.7);
		}

		&.is-active {

			color: #333333!important;

			path {
				stroke: #333333!important;
			}
		}
	}

	.main-nav__dropdown {
		background-color: #ebebeb;

		.card h2 span {
			font-weight: 700;
		}
	}

	nav {
		@media (max-width: 768px) {
			background: linear-gradient(0deg, rgba(18, 18, 18, 0.08), rgba(18, 18, 18, 0.08)), #F4F4F4;
		}
	}

	@media(max-width: 1024px) {
		.card__img-wrapper {
			width: 64px;
		}
	}
	@media (max-width: 768px) {

		.mobile-heading {
			p {
				color: #333333!important;
			}
		}

		.mobile-links {
			a {
				color: #333333!important; 
			}
		}

		.bars{

			span {
				background-color: #333333;
			}

			&:hover,
			&:focus {

				span {
					background: #333333;
				}
			}
		}
	}
}