.button {
  -webkit-font-smoothing: inherit;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  color: $color-base;
  transition: background-color .2s ease, color .2s ease;
  border: none;
  padding: 0;
  margin: 0;
  outline: none;
  transition: all .2s ease;
  background-color: transparent;
  cursor: pointer;
  font-family: $base-font-family;
  text-decoration: none;
  line-height: 1;
  font-weight: 700;
  white-space: nowrap;

  &:not(:disabled) {

    &:hover {}

    &:focus {}

    &:active {
      outline: none;
      box-shadow: none !important;
      text-decoration: none;
    }
  }

  &:disabled {
    opacity: .5;
    cursor: not-allowed !important;
  }
}


.button--purple {
  display: inline-flex;
  align-items: center;
  height: 46px;
  width: auto;
  color: $color-btn;
  font-size: $base-font-size;
  text-decoration: none;
  background-color:  #A467DE;
  padding: $btn--primary-padding;

  &:not(:disabled) {

    &:hover,
    &:focus,
    &:active {
      background-color:  darken(#A467DE, 15%);
      color: #fff;
    }
  }
}

