

.contact-section {
	padding: 264px 0 140px;
	background-color: #f4f4f4;

	h1 {
		margin-bottom: 40px;
		@extend .hideUp;
		transition: all 1s ease;
	}

	p.large-text {
		color: #333333;
		margin-bottom: 140px;
		margin-top: 60px;
		@extend .hideUp;
		transition: all 1s ease 1.2s;
	}

	.container {
		display: flex;
		flex-wrap: wrap;
	}

	article {
		flex: 0 0 50%;


	}

	.sticky-img {
		display: flex;
		justify-content: flex-end;
		align-items: flex-start;

	}

	.img-wrapper { 
		display: flex;
		justify-content: flex-end;

		.lottie {
			height: 480px;
			width: 470px;
			position: relative;

			>div {
				right: 0;
			}
		}

		img.sticker5 {
			width: 400px;
			// position: -webkit-sticky;
			// position: sticky;
			// top: 50%;
			z-index: 2;
		}
	}

	&.active {
		h1, form, .large-text, .contact__suggested, .social-box {
			@extend .fadeInUp;
		}
	}

	.img-mobile {
		display: none;
	}

	form {
		@extend .hideUp;
		transition: all 1s ease .8s;
	}

	@media(max-width: 1439px) and (min-width: 1025px) {
		padding: 224px 0 120px;

		h1 {
			margin-bottom: 30px;
		}

		.input-box {
			margin-bottom: 15px;
		}

		.input--primary {
			height: 52px;
		}

		.input-box--button button {
			height: 36px;
			padding: 0 12px;
			font-size: 14px;
			line-height: 19.6px;
		}
	}

	@media(max-width: 1024px) {
		padding: 186px 0 100px;

		.container {
			flex-direction: column;
			align-items: center;
		}

		article {
			// flex: 0 0 50%;
			width: 450px;
			max-width: 100%

		}

		h1 {
			text-align: center;
		}

		p.large-text {
			margin-bottom: 37px;
			max-width: 340px;
			margin: 40px auto 100px;
			text-align: center;
		}

		.img-wrapper { 
			margin: 40px auto;
			width: 264px;
			max-width: 100%;
			display: block;
		}

		.sticky-img {
			display: none;
		}

		.input-box {
			margin-bottom: 15px;
		}

		.input--primary {
			padding: 0 15px;
			height: 52px;
			font-size: 14px;
			line-height: 19.6px;
		}

		.input--textarea {
			padding: 15px;
			font-size: 14px;
			line-height: 19.6px;
		}

		.button {
			height: 36px;
			padding: 0 12px;
			font-size: 14px;
			line-height: 19.6px;
		}

		.uk-sticky-placeholder {
			display: none;
		}
	}

	@media(max-width: 610px) {
		padding: 111px 0 80px;

		h1 {
			font-size: 32px;
			line-height: 35.2px;
			margin-bottom: 30px;
		}

		.container {
			padding: 0;
		}

		p.large-text {
			margin-bottom: 30px;
			margin-top: 30px;
		}

		.img-wrapper { 
			margin: 0 auto 80px;
		}
	}
}

.contact__suggested {
	display: flex;
	margin-bottom: 30px;
	@extend .hideUp;
	transition: all 1s ease .4s;

	p {
		margin-bottom: 0;
		// margin-right: 10px;
		color: #333333;
	}

	ul {
		display: flex;
		flex-wrap: wrap;
		margin-bottom: 0;

		li {
			margin-right: 10px;
			margin-bottom: 5px;
		}

		span {
			display: flex;
			align-items: center;
			white-space: nowrap;
			background: #EAEAEA;
			color: #A467DE;
			font-size: 14px;
			font-weight: 700;
			line-height: 16.8px;
			height: 25px;
			padding: 0 8px;
			text-transform: uppercase;

		}
	}

	@media(max-width: 1439px) and (min-width: 1025px) {
		margin-bottom: 20px;

		ul {
			span {
				font-size: 12px;
				height: 22px;
				line-height: 14.4px;

			}
		}
	}

	@media(max-width: 1024px) {
		margin-bottom: 15px;

		ul {
			p {
				font-size: 14px;
				line-height: 22.4px;
			}
			span {
				font-size: 12px;
				line-height: 14.4px;
				height: 22px;

			}
		}
	}

	@media(max-width: 610px) {
		margin-bottom: 5px;

		ul {
			display: flex;
			flex-wrap: wrap;
			margin-bottom: 0;

			li {
				margin-right: 10px;
				margin-bottom: 10px;
			}
		}
	}
}

.social-box {
	background: #EAEAEA;
	padding: 80px;
	text-align: center;

	@extend .hideUp;
	transition: all 1s ease 1.6s;

	h3 {
		margin-bottom: 30px;
	}

	ul {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-bottom: 0;
	}

	li {
		margin: 0 13px;

		a {
			display: flex;
			align-items: center;
			color: #2B2B2B;
			font-size: 32px;

			&:hover {
				color: #7315CD;
			}
		}
	}

	@media(max-width: 1024px) {
		padding: 60px 20px;

		h3 {
			margin-bottom: 20px;
		}
	}

	@media(max-width: 610px) {
		padding: 40px 5px;

	}

}

.three-column-box--contact {
	background: #EAEAEA;
	padding: 140px 0 120px;

	@media(max-width: 1024px) {
		padding: 80px 0;
	}

	@media(max-width: 610px) {
		padding: 40px 0;
	}
}