/* ------------------------------------*\
    #PAGE
\*------------------------------------ */


html {
  min-height: 100vh;
  color: $color-text;
  font-family: $base-font-family;
  font-size: $base-font-size;
  line-height: $base-line-height;
  font-weight: 400;

  &.active {
  	overflow: hidden;
  }
}

main {
	overflow: hidden;
}

body {
  	overflow-x: hidden;

	@media(max-width: 1024px) {
		padding-bottom: 75px;

		&.career,
		&.contact-page {
			padding-bottom: 0;
		}
	}

	@media(max-width: 610px) {
		padding-bottom: 66px;

		&.career,
		&.contact-page {
			padding-bottom: 0;
		}
	}
}

address,
h1,
h2,
h3,
h4,
h5,
h6,
blockquote,
p,
pre,
dl,
ol,
ul,
figure,
hr,
table,
fieldset {
  margin-bottom: $spacing-unit;
}

img {
  max-width: 100%;
}

svg {
	// transition: all .2s ease;

	& * {
		transition: all .2s ease;
	}
}

p {
	font-size: 16px;
	line-height: 25.6px;

	&.large-text {
		font-size: 24px;
		line-height: 33.6px;

		@media(max-width: 1440px) {
			font-size: 18px;
			line-height: 25.2px;

			br {
				display: none;
			}
		}
	}

	@media(max-width: 1440px) {
		font-size: 14px;
		line-height: 22.4px;
	}
}

strong {
	font-weight: 700;
}